body {
    margin: 0;
    padding: 0;
    background: #f7f7f7;
}

.Login {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    height: 400px;
    background: #fff;
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0 20px 50px rgba(0, 0, 0, .5);
    border: 2px solid rgba(0, 0, 0, .5);
    transition: all 0.25s;
    &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
        background: rgba(255, 255, 255, 0.1);
        transition: .5s;
        pointer-events: none;
        background: #009688;
    }
    &:hover {
        &:before {
            height: 15%;
        }
    }
    &__content {
        position: absolute;
        top: 15px;
        left: 15px;
        right: 15px;
        bottom: 15px;
        border: 2px solid #009688;
        padding: 30px;
        text-align: center;
        box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
        h1 {
            color: #fff;
        }
        .form-control {
            color: #fff;
            width: 100%;
            background: transparent;
            outline: none;
            padding: 3px 0;
            border: none;
            font-size: 1rem;
            &::placeholder {
                color: rgba(0, 0, 0, .3);
            }
        }
        .btn-login {
            padding: 10px 20px;
            font-size: 1.1rem;
            cursor: pointer;
            text-transform: uppercase;
            font-family: 'Roboto', sans-serif;
            background: #009688;
            border: 1px solid #009688;
            color: #fff;
            transition: .5s all;
            border-radius: 5px;
            outline: none;
            &:hover {
                background: #009688 - 50;
                color: #fff;
                transition: .5s all;
            }
        }
    }
    &__footer {
        position: absolute;
        top: 92%;
        right: 10%;
        transition: 1s all;
        .link {
            color: #009688;
            text-decoration: none;
            &:hover {
                color: #dde6e5 - 40;
            }
        }
    }
    &:hover &__footer {
        transition: 1s all;
        opacity: 1;
        .link {
            color: #fff;
        }
    }
    .span-line {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
        box-sizing: border-box;
        &:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 2px;
            background: #009688;
            animation: animate 4s linear infinite;
        }
        &:nth-child(1) {
            transform: rotate(0deg);
        }
        &:nth-child(2) {
            transform: rotate(90deg);
            &:before {
                animation-delay: -2s;
            }
        }
        &:nth-child(3) {
            transform: rotate(180deg);
        }
        &:nth-child(4) {
            transform: rotate(270deg);
            &:before {
                animation-delay: -2s;
            }
        }
    }
}

@media (min-width: 540px) {
    .Login {
        width: 400px;
        height: 400px;
    }
}

@media (max-width: 540px) {
    .Login {
        width: 280px;
        height: 350px;
        &__content {
            position: absolute;
            top: 5px;
            left: 5px;
            right: 5px;
            bottom: 5px;
            border: 2px solid #009688;
            padding: 5px;
            text-align: center;
            box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
            h1 {
                font-size: 2rem;
            }
            .form-control {
                color: #fff;
                width: 90%;
                font-size: 1rem;
                &::placeholder {
                    color: rgba(0, 0, 0, .3);
                }
            }
            .btn-login {
                padding: 5px 10px;
                font-size: 1.1rem;
                &:hover {
                    background: transparent;
                    color: #009688;
                    transition: .5s all;
                }
            }
        }
        &__footer {
            top: 90%;
            right: 5%;
            .link {
                font-size: 0.8rem;
            }
        }
    }
}

@keyframes animate {
    0% {
        transform: scaleX(0);
        transform-origin: left;
    }
    50% {
        transform: scaleX(1);
        transform-origin: left;
    }
    50.1% {
        transform: scaleX(1);
        transform-origin: right;
    }
    100% {
        transform: scaleX(0);
        transform-origin: right;
    }
}